.header {
  padding: 32px 0;
  background-color: #fff;
}
.header__wrap {
  display: flex;
  align-items: center;
  position: relative;
  justify-content: space-between;
}
.header .logo {
  max-width: 185px;
}
.header .nav {
  margin-right: 0;
  padding: 0 10px;
}
.header .nav__item {
  color: #243970;
  font-size: 13px;
  font-weight: 700;
  text-transform: uppercase;
}
.header .nav__item:not(:last-of-type) {
  margin-right: 12px;
}
.header .phone {
  color: #1074b9;
  font-weight: 700;
  font-size: 25px;
  text-align: right;
  align-self: flex-start;
}
.header .phone .time {
  font-size: 12px;
  font-weight: 400;
  display: block;
}
.header .phone .link {
  font-weight: 700;
  font-size: 15px;
}
.header .lang {
  position: relative;
  border: 1px solid #e9ebf0;
  padding: 0 9px 0 11px;
  height: 31px;
  border-radius: 12px;
  display: flex;
  align-items: center;
}
.header .lang__content {
  padding-right: 3px;
  display: flex;
  flex-direction: column;
}
.header .lang__item {
  display: none;
  font-size: 13px;
  font-weight: 700;
  color: #243970;
}
.header .lang__item.current {
  display: block;
}
.header .lang__toggle {
  width: 8px;
  height: 5px;
  background-image: url("../img/icons/lang-arrow.png");
  background-size: cover;
  background-position: center;
  cursor: pointer;
}
.header .burger {
  position: absolute;
  display: none;
  right: 0;
  top: 0;
  width: 30px;
  height: 21px;
  z-index: 4;
  border-radius: 100px;
}
.header .burger:before {
  content: '';
  position: absolute;
  box-shadow: 0 0 0 0 #6fbcbc;
  transition: 1s;
  width: 2px;
  height: 2px;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  border-radius: 100px;
  display: block;
}
.header .burger.active:before {
  box-shadow: 0 0 0 5000px #6fbcbc;
}
.header .burger.active span {
  background-color: #fff;
}
.header .burger.active span:nth-of-type(1) {
  transform: rotate(45deg);
  top: 9px;
}
.header .burger.active span:nth-of-type(2) {
  opacity: 0;
}
.header .burger.active span:nth-of-type(3) {
  transform: rotate(-45deg);
  bottom: 9px;
}
.header .burger span {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 3px;
  background-color: #6fbcbc;
  display: block;
  border-radius: 100px;
  transition: 0.5s;
  transform: rotate(0);
  opacity: 1;
}
.header .burger span:nth-of-type(1) {
  bottom: auto;
}
.header .burger span:nth-of-type(3) {
  top: auto;
}
.header .mobile-menu {
  position: fixed;
  top: 50px;
  right: 0;
  height: calc(100vh - 50px);
  overflow: auto;
  width: 100%;
  z-index: 4;
  padding: 0 40px 40px;
  visibility: hidden;
  opacity: 0;
  transform: translateY(-20px);
  transition: 0.5s;
  transition-delay: 0.2s;
}
.header .mobile-menu.active {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}
.header .mobile-menu .nav {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0;
}
.header .mobile-menu .nav__item {
  border: none;
  color: #fff;
  padding: 8px 0;
  font-size: 17px;
}
.header .mobile-menu .nav__item:not(:first-of-type) {
  margin-left: 0;
}
.header .mobile-menu .phone {
  display: inline-block;
  color: #fff;
  margin-top: 40px;
}
